.angledBars{
    display: inline-flex;
    width:1px;
    height:450px;
    background-color: black;
    transform-origin: bottom;
    
    }
    
    .sortarena{
        background-color: #F9F7F7;
    border-radius: 10px;
    margin-top: 10px;
    left:6%;
    right:6%;
    margin-bottom: 30px;
    
    
    }
    
    
    .Navbar{
        background-color: rgb(0, 100, 139);
        color: white;
        display: flex;
        justify-content: space-around;
        margin: 25px;
        padding:20px
    }
    
    @media screen and  (max-width:500px) {
        .angledBars{
            height:150px;
        }
        
    }
    
    
    @media screen and  (min-width:500px )and ( max-width:800px) {
        .angledBars{
            height:250px;
        }
        
    }